body {
  font-family: "Inter", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  color: #FFFFFF;
  font-size: 2rem;
}

body > section {
  padding: 5rem 0;
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.texto {
  color: white;
}

.gris-oscuro {
  background-color: #101010;
}

.gris {
  background-color: #151515;
}

h1, h2, h3, p {
  color: #FFFFFF;
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

.header {
  background-color: #3A3A3A;
  padding: 2rem 0;
}
.header .contenido-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 2rem;
  margin-top: 1.4rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    flex-direction: unset;
    justify-content: space-between;
    font-size: unset;
    margin-top: unset;
  }
}
.header .contenido-header a {
  z-index: 40;
}
.header .navegacion-principal {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  gap: 1.5rem;
  display: none;
  z-index: 45;
}
@media (min-width: 768px) {
  .header .navegacion-principal {
    flex-direction: unset;
    gap: 2rem;
    font-weight: 700;
    display: flex;
    margin-top: 2rem;
    font-size: 1.7rem;
  }
}
.header .navegacion-principal a {
  border: 2px solid transparent;
}
.header .navegacion-principal a:hover {
  border: 2px solid #6A6A6A;
}

.fondo {
  background-image: url("/build/img/mountain1.jpg"), image-set(url("/build/img/mountain1.avif") 1x, url("/build/img/mountain1.webp") 1x, url("/build/img/mountain1.jpeg") 1x);
  background-repeat: no-repeat;
  background-position: center center;
  display: block;
  min-height: 100rem;
  max-height: 120rem;
  position: relative;
  z-index: 0;
}
.fondo .overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
@media (min-width: 768px) {
  .fondo .overlay {
    inset: 0;
    display: flex;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .fondo .portada {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.fondo .portada .imagen {
  max-width: 50%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  margin-top: 12rem;
}
@media (min-width: 768px) {
  .fondo .portada .imagen {
    display: flex;
    flex-direction: unset;
    justify-content: center;
    max-width: unset;
    top: unset;
    left: unset;
    right: unset;
    bottom: unset;
    margin: unset;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .fondo .portada .imagen img {
    max-width: 50%;
  }
}
.fondo .descripcion {
  text-align: center;
}
@media (min-width: 768px) {
  .fondo .descripcion {
    text-align: unset;
  }
}
.fondo .descripcion h2 {
  margin-top: 4rem;
  color: #FFFFFF;
  font-size: 7rem;
  padding: 1rem;
}
@media (min-width: 768px) {
  .fondo .descripcion h2 {
    margin-top: 0;
  }
}
.fondo .descripcion p {
  color: #FFFFFF;
  display: block;
  padding: 1rem;
  margin-top: 1rem;
  font-weight: 400;
}
@media (min-width: 768px) {
  .fondo .descripcion p {
    margin-top: 0;
  }
}

.footer {
  padding: 3rem;
  text-align: center;
}

.gris-oscuro {
  background-color: #101010;
}

@media (min-width: 768px) {
  .categorias {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .categorias {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.categoria {
  padding: 4rem;
}
.categoria :not(:first-of-type) {
  margin-top: 1rem;
}
.categoria p {
  margin-top: 2.5rem;
  text-align: center;
}
.categoria p .negrita {
  font-weight: bold;
  font-size: 3rem;
  display: block;
  text-align: center;
  margin-bottom: 2rem;
}
.categoria .imagen-categoria {
  padding: 6rem 6rem 2rem 6rem;
}
@media (min-width: 768px) {
  .categoria .imagen-categoria {
    padding: 3rem;
  }
}
@media (min-width: 768px) {
  .categoria {
    padding: 2rem;
    justify-items: center;
    align-items: center;
  }
}

.redes {
  padding: 3rem;
}
.redes .elementos-red {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .redes .elementos-red {
    flex-direction: row;
    justify-content: center;
  }
}
.redes .elementos-red i {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 3rem;
}
.redes .elementos-red a span {
  margin-top: 1rem;
  display: inline-block;
}